import { useState } from "react";
import { Link } from "react-router-dom";
import { FaTimes } from "react-icons/fa";

import Box from "@mui/material/Box";
import Alert from "@mui/material/Alert";
const Login = ({
  handleLogin,
  loginLoading,
  loginFail,
  handleCloseLog,
  invalidEmail,
  handleCloseEmail,
}) => {
  const [emailInputLogin, setEmailInputLogin] = useState("");
  const [passInputLogin, setPassInputLogin] = useState("");
  const [showPass, setShowPass] = useState(false);
  return (
    <Box className="">
      {loginFail ? (
        <Box className="alert-box bg-light-red p-3">
          Login failed
          <FaTimes
            className="align-right-icon"
            onClick={() => {
              handleCloseLog();
            }}
          />
        </Box>
      ) : (
        ""
      )}
      {invalidEmail ? (
        <Box className="alert-box bg-light-red p-3">
          Invalid Email Address
          <FaTimes
            className="align-right-icon"
            onClick={() => {
              handleCloseEmail();
            }}
          />
        </Box>
      ) : (
        ""
      )}
      {loginLoading ? (
        <Box className="alert-box p-3 bg-primary text-white">Logging in...</Box>
      ) : (
        ""
      )}
      <Box className="">
        <h2>Login</h2>
        <label className="h5">Email</label>
        <input
          type="text"
          className="form-control"
          placeholder="Email"
          value={emailInputLogin}
          onChange={(e) => {
            setEmailInputLogin(e.target.value);
          }}
        />
        <label className="h5">Password</label>
        <input
          type={showPass ? "text" : "password"}
          className="form-control"
          placeholder="Password"
          value={passInputLogin}
          onChange={(e) => {
            setPassInputLogin(e.target.value);
          }}
        />
        <input
          type="checkbox"
          checked={showPass}
          onChange={() => {
            setShowPass(!showPass);
          }}
          id="passCheckBox"
        />
        <label htmlFor="passCheckBox">Show password</label>
        <button
          className="btn text-white login-register-button my-2 d-block w-100"
          onClick={(e) => {
            handleLogin(emailInputLogin, passInputLogin);
          }}
        >
          Login
        </button>
        <Link to={{ pathname: "./register" }} className="d-block">
          Don't have an account? Register here
        </Link>
      </Box>
    </Box>
  );
};

export default Login;
