import React from "react";
import FormControl from "@mui/material/FormControl";

import TextField from "@mui/material/TextField";

const Counter = ({ handleChangeCount, fruitCount }) => {
  return (
    <FormControl>
      <TextField
        size="small"
        inputProps={{ min: 1 }}
        type="number"
        label="Count"
        id="select-count"
        value={fruitCount}
        onChange={(e) => {
          handleChangeCount(parseInt(e.target.value, 10));
        }}
      ></TextField>
    </FormControl>
  );
};

export default Counter;
