import { useState } from "react";
import { Link } from "react-router-dom";
import { FaTimes } from "react-icons/fa";

import Alert from "@mui/material/Alert";

const Register = ({
  handleRegister,
  regLoading,

  handleCloseReg,
  handleInvalidRegister,
  invalidRegister,
  handleInvalidRegisterClose,
  passwordMismatch,
  setPasswordMismatch,
}) => {
  const [emailInput, setEmailInput] = useState("");
  const [passInput, setPassInput] = useState("");
  const [passConfirmInput, setPassConfirmInput] = useState("");

  const [nameInput, setNameInput] = useState("");
  const [showPass, setShowPass] = useState(false);
  return (
    <div className="">
      {regLoading ? (
        <div className="alert-box bg-primary p-3 text-white">
          Registering...
        </div>
      ) : (
        ""
      )}

      {invalidRegister ? (
        <div className="alert-box bg-light-red p-3">
          Invalid Email Address
          <FaTimes
            className="align-right-icon"
            onClick={() => {
              handleInvalidRegisterClose();
            }}
          />
        </div>
      ) : (
        ""
      )}
      <h2>Register</h2>
      <label className="h5 d-block">Name</label>
      <input
        type="text"
        className="form-control"
        placeholder="Name"
        value={nameInput}
        onChange={(e) => {
          setNameInput(e.target.value);
        }}
      />
      <label className="h5 mt-2">Email</label>
      <input
        type="email"
        className="form-control"
        placeholder="Email"
        value={emailInput}
        onChange={(e) => {
          setEmailInput(e.target.value);
        }}
      />
      <label className="h5 mt-2">Password</label>
      <input
        type={showPass ? "text" : "password"}
        className="form-control"
        placeholder="Password"
        value={passInput}
        onChange={(e) => {
          setPassInput(e.target.value);
        }}
      />
      <label className="h5 mt-2">Confirm Password</label>
      <input
        type={showPass ? "text" : "password"}
        className="form-control"
        placeholder="Password"
        value={passConfirmInput}
        onChange={(e) => {
          setPassConfirmInput(e.target.value);
        }}
      />
      <input
        className="mt-2"
        type="checkbox"
        checked={showPass}
        onChange={() => {
          setShowPass(!showPass);
        }}
        id="passCheckBox"
      />
      <label htmlFor="passCheckBox">Show password</label>
      {passwordMismatch ? (
        <Alert
          severity="error"
          className="my-3"
          onClose={() => {
            setPasswordMismatch(false);
          }}
        >
          Passwords do not match
        </Alert>
      ) : (
        ""
      )}

      <Link to={{ pathname: "" }}>
        <button
          className="btn text-white login-register-button my-2 d-block w-100"
          onClick={() => {
            handleRegister(emailInput, passInput, nameInput, passConfirmInput);
          }}
        >
          Register
        </button>
      </Link>
      <Link to={{ pathname: "/" }}>Have an account? Login here</Link>
      {/* <button
        onClick={() => {
          console.log(emailInput, passInput, nameInput);
        }}
      >
        test
      </button> */}
    </div>
  );
};

export default Register;
