import React from "react";

import TransactionRow from "./TransactionRow";

import Box from "@mui/material/Box";

import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";

const Transaction = ({ data }) => {
  return (
    <>
      User: {data.userName}
      {/* {data.userId} */}
      <List>
        <ListItem>
          <Box className="col-3 text-center">Item</Box>
          <Box className="col-3 text-center">Price</Box>
          <Box className="col-3 text-center">Count</Box>
          <Box className="col-3 text-center">Total Price</Box>
        </ListItem>
        {data.items.length > 0
          ? data.items.map((item, index) => (
              <TransactionRow key={index} item={item} />
            ))
          : ""}
        <ListItem>
          <Box className="col-3 text-center"></Box>
          <Box className="col-3 text-center"></Box>
          <Box className="col-3 text-center">Final Bill</Box>
          <Box className="col-3 text-center">
            <strong>{data.finalBill}</strong>
          </Box>
        </ListItem>
      </List>
    </>
  );
};

export default Transaction;
